import b2bFooter from "../../components/b2bfooter.vue";
 
import shopItem from "../../components/shop-item.vue";
import b2bSortbox from "../../components/b2b-sortbox.vue";
import {mapState,mapMutations} from 'vuex'
export default {
	components: {
		b2bFooter,
		b2bSortbox, 
		shopItem
	},
	data() {
		return {
			localAddress:"厦门瑞景商业广场",
			pageData: {},
			keyword: "",
			pageLoad: false,
			shopList: {},
			isFirst:true,
			per_page:0,
			discount:"",
			price:"",
			choice:"",
			orderby:""
		}
	},
	onLoad: function(ops) {
		this.localAddress=this.app.getLocalAddress();
		this.getPage();
		this.getList();

	},
	onPageScroll: function(e) {
		var that = this;
		this.$store.commit("setScrollTop",e.scrollTop);
	},
	onPullDownRefresh:function(){
		this.getPage();
		uni.stopPullDownRefresh();
	},
	onReachBottom:function(){
		this.getList();
	},
	methods: {
		sortParent:function(ops){
			this.discount=ops.discount;
			this.price=ops.price;
			this.choice=ops.choice;
			this.orderby=ops.orderby;
			this.per_page=0;
			this.isFirst=true;
			console.log(ops)
			this.getList();
		},
		goAddr:function(){
			uni.navigateTo({
				url:"../../pageb2b/b2b_addr/index"
			})
		},
		search: function() {
			uni.navigateTo({
				url: "../../pageb2b/b2b_search/index?keyword=" + encodeURI(this.keyword)
			})
		},
		goShop: function(shopid) {
			uni.navigateTo({
				url: "../../pageb2b/b2b_shop/index?shopid=" + shopid
			})
		},
		goProduct: function(id) {
			uni.navigateTo({
				url: "../../pageb2b/b2b_product/show?id=" + id
			})
		},
		gourl: function(url) {
			uni.navigateTo({
				url: url
			})
		},
		getPage: function() {
			var latlng=this.app.getGps();
			var that = this;
			that.app.get({
				url: this.app.apiHost + "/module.php?m=b2b&ajax=1",
				data:{
					lat:latlng.lat,
					lng:latlng.lng
				},
				success: function(res) {						
					that.pageData = res.data;
					that.pageLoad = true;
					
				}
			})
		},
		
		getList: function() {
			var that = this;
			var latlng=this.app.getGps();
			if(that.per_page==0 && !that.isFirst){
				return false;
			}
			
			that.app.get({
				url: that.app.apiHost + "/module.php?m=b2b_shoplist&a=list&ajax=1",
				data: {
					discount:that.discount,
					price:that.price,
					choice:that.choice,
					orderby:that.orderby,
					lat:latlng.lat,
					lng:latlng.lng
				},
				dataType: "json",
				success: function(res) {
					 
					
					that.per_page=res.data.per_page;
					if(that.isFirst){
						that.isFirst=false;
						that.shopList = res.data.shopList;
					}else{
						var list=that.shopList;
						for(var i in res.data.shopList){
							list.push(res.data.shopList[i]);
						}
						that.shopList=list;
					}
				}
			})
		}
	}
}
